


























export default {
	name: 'uni-swiper-dot',
	props: {
		info: {
			type: Array,
			default () {
				return []
			}
		},
		current: {
			info: Number,
			default: 0
		},
		dotsStyles: {
			type: Object,
			default () {
				return {}
			}
		},
		// 类型 ：default(默认) indexes long nav
		mode: {
			type: String,
			default: 'default'
		},
		// 只在 nav 模式下生效，变量名称
		field: {
			type: String,
			default: ''
		}
	},
	data() {
		return {
			dots: {
				width: 5,
				height: 5,
				bottom: 10,
				color: '#fff',
				backgroundColor: 'rgba(255, 255, 255, .3)',
				border: '1px rgba(0, 0, 0, .3) solid',
				selectedBackgroundColor: 'rgba(255, 255, 255,0.5)',
				selectedBorder: '1px rgba(78, 184, 125, .9) solid'
			}
		};
	},
	created() {
		if (this.mode === 'indexes') {
			this.dots.width = 15
			this.dots.height = 15
		}
		this.dots = Object.assign(this.dots, this.dotsStyles)
	},
	watch: {
		dotsStyles(newVal) {
			this.dots = Object.assign(this.dots, this.dotsStyles)
		},
		mode(newVal) {
			if (newVal === 'indexes') {
				this.dots.width = 15
				this.dots.height = 15
			} else {
				this.dots.width = 5
				this.dots.height = 5
			}
		}

	}
}
